body {
    font-family: $mainFont;
    font-size: 14px;
    
}

    // reset styles
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
        margin: 0;
        padding: 0;
    }

    // responsive images
    img {
        display: block;
        max-width: 100%;
        height: auto;
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    a {
        text-decoration: none;
    }